import API from './fetchAPI';
import Swiper, { Navigation, Pagination, Autoplay } from 'swiper';
import { refs } from './refs';
import onOpenModal from './renderModal';
Swiper.use([Pagination, Navigation, Autoplay]);
import { lazyLoad } from './lazyLoadImg';



function murkUpSlider({ id, poster_path, vote_average, title }) {
  let poster = poster_path
    ? `https://image.tmdb.org/t/p/w500/${poster_path}`
    : 'https://www.hpl24.pl/userdata/public/gfx/f6194102ce247a5d6891a7b039fc49ad.jpg';
  if (vote_average >= 7) {
    return `<li class="swiper-item swiper-slide movie-card" data-id=${id}>
            <a href="" class="slider-link">
                     <picture>
                        <source class="lzy_img" media="(min-width: 1200px)" 
                        srcset=""  type="image/jpeg" width="155" height="233" data-src= "${poster} 1x,${poster} 2x">
                                                       
      
                         <source class="lzy_img" media="(min-width: 768px)"
                         srcset=""  type="image/jpeg" width="115" height="170" data-src= "${poster} 1x,${poster} 2x">
                    <img class="slider-image"
                        src="${poster}"
                        alt="${title}"
                        loading="lazy"
                    />
                 </picture>
            </a>
         </li>`;
  }
}

const options = {

  initialSlide: 0,
  Observer: true, // –ü—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Å–∞–º–æ–≥–æ swiper –∏–ª–∏ –µ–≥–æ –¥–æ—á–µ—Ä–Ω–∏—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ swiper –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç—Å—è
  Parents: true, // Swiper –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ—Ç—Å—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–æ–¥–∏—Ç–µ–ª—å—Å–∫–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ swiper
  onSlideChangeEnd: function (swiper) {
    swiper.update(); // –û–±–Ω–æ–≤–ª—è–µ–º Swiper, —ç—Ç–æ—Ç –º–µ—Ç–æ–¥ –≤–∫–ª—é—á–∞–µ—Ç –≤ —Å–µ–±—è –º–µ—Ç–æ–¥—ã updateContainerSize, updateSlidesSize, updateProgress, updatePagination, updateClasses. –¢–æ –µ—Å—Ç—å –∏–∑–º–µ–Ω–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –∑–∞–∫–ª—é—á–∞–µ—Ç—Å—è –≤ –ø–æ–≤—Ç–æ—Ä–Ω–æ–π –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏ —Å–≤–∞–π–ø–µ—Ä–∞;
    swiperSlider.startAutoplay(); // –ü–µ—Ä–µ–∑–∞–ø—É—Å–∫ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–≥–æ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è;
    swiperSlider.reLoop(); // –ü–µ—Ä–µ—Å—á–∏—Ç—ã–≤–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–ª–∞–π–¥–æ–≤, –∫–æ—Ç–æ—Ä—ã–µ –Ω—É–∂–Ω–æ –∑–∞—Ü–∏–∫–ª–∏—Ç—å. –≠—Ç–æ –Ω—É–∂–Ω–æ, –∫–æ–≥–¥–∞ –≤—ã –º–µ–Ω—è–µ—Ç–µ –ø–∞—Ä–∞–º–µ—Ç—Ä slidesPerView, –∏ –µ–≥–æ –Ω—É–∂–Ω–æ –¥–æ–±–∞–≤–ª—è—Ç—å, –∫–æ–≥–¥–∞ –≤–∞–º –Ω—É–∂–Ω–æ –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –ø–æ–≤–µ—Ä–Ω—É—Ç—å;
  },
  loop: true,
  slidesPerView: 6,
  spaceBetween: 5,
  autoplay: {
    delay: 1500,
    stopOnLastSlide: false,
    disableOnInteraction: false,
  },
  on: {
    init() {
      this.el.addEventListener('mouseenter', () => {
        this.autoplay.stop();
      });
      this.el.addEventListener('mouseleave', () => {
        this.autoplay.start();
      });
    }
  },
};



function appendGallery(data) {
  refs.sliderList.innerHTML = data;
  const swiperSlider = new Swiper('.slider-swiper', options);
  swiperSlider.on('click', () => {
    refs.sliderList.addEventListener('click', onOpenModal);
  });
}

// let newArray = [];

function getFilmSlider() {
    
  if (window.innerWidth < 768) {
    return;
  }
  
  // for (let page = 1; page <= 5; page += 1) {
    
    API.getMoviesByTrending(5)
      .then(data => {
        // newArray.push(data);  
        // console.log(newArray);
        return renderMovie(data)
      })
      .then(appendGallery)
      .catch(error => {
        console.log(error);
      });
  
  // }
}


function renderMovie(data) {
  let dataRender = data.results || data;
  return dataRender.map(d => murkUpSlider(d)).join(' ');
}


